/* --blue-light-1: #fff;
  --blue-light-2: #8DA0BC;
  --blue-medium-1: #4F6B95; 
  --blue-medium-2: #007AFF; 
  --blue-medium-3: #1e2747;
  --blue-dark-1: #141A32; 
  --blue-dark-2: #080F28;  */
.TodoItem {
    background-color: #080F28;
    color: #8DA0BC;
    border-radius: 15px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}
span {
    color: #fff;
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}
.TodoItem-p--complete {
    text-decoration: line-through;
    color: #080F28;
}
.TodoItem--complete {
    background-color: #007AFF;
}